#base-banner{
    position: relative;
    display: inline-block;
    margin-bottom: 3%;
}

#button{
    display: flex;
    position: absolute;
    top: 70%;
    left: 80%;
    transform: translate(-50%, -50%);
    padding: 10px 20px;
    background-color: #539ddb;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    width: 15%;
    height: 6%;
    font-size: 20px;
    align-items: center;
    justify-content: center;
}

#banner{
    width: 100%;
}

.base1{
    display: flex;
    justify-content: space-evenly;
}

.texto{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 0 12%;
    margin-bottom: 5%;
}

#office{
    margin-left: 10%;
}

#office-mobile{
    display: none;
}

.Titulo{
    font-size: 40px;
}

#objetivo{
    margin-right: 15%;
    margin-bottom: 5%;
}

#objetivo-mobile{
    display: none;
}

.footer{
    display: flex;
    align-items: center;
    background: #000;
    padding: 1% 0;
    width: 100%;
    justify-content: center;
}

.base-icons{
    display: flex;
    flex-direction: column;
    margin-right: 10rem;
}

#base-feedback{
    display: flex;
    flex-direction: column;
    margin-bottom: 5%;
    justify-content: center;
    align-items: center;
}

#base-input{
    display: flex;
    flex-direction: column;
}

.feedback{
    border: 3px solid white;
    border-radius: 5px;
    background: none;
    width: 20rem;
    resize: vertical;
    outline: none;
    margin-bottom: 3%;
    height: 2rem;
    padding-left: 1.5%;
    color: white;
}

#Titulo{
    color: white;
}

#button-feedback{
    border: none;
    color: white;
    height: 2rem;
    width: 103%;
    background-color: #539eda;
    border-radius: 5px;
    cursor: pointer;
}

#button-feedback:hover{
    color: white;
    background-color: #63a4da;
}

.vertical-line {
    border: none;
    border-left: 2px solid white;
    height: 12rem;
    margin: 0 2%;
  }
  
#facebook{
    margin-bottom: 2rem;
}

@media screen and (max-width: 730px){
    #button{
        display: none;
    }

    #banner{
        display: none;
    }

    .Home{
        display: flex;
        flex-direction: column;
    }

    .base1{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column; 
    }

    #office{
        margin-left: 0;
        width: 65%;
    }

    #objetivo{
        display: none;
    }

    #objetivo-mobile{
        display: block;
        width: 65%;
    }

    #office{
        display: none;
    }

    #office-mobile{
        display: block;
        width: 65%;
    }

    #base-feedback{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;padding: 0 10%;
    }    

    .feedback{
        display: none;
    }

    #Titulo{
        display: none;
    }

    #button-feedback{
        display: none;
    }

    .vertical-line{
        display: none;
    }
}